@import url("https://fonts.googleapis.com/css?family=Work+Sans:300,400,600,700");
body {
  margin: 0;
  padding: 0;
  font-family: "Work Sans", sans-serif;
  font-weight: normal;
  color: #424242; }

.text {
  margin: 0;
  line-height: 1.75;
  font-weight: normal; }
