    .text {
      font-size: 16px;
      letter-spacing: -0.6px; } }
  @media (max-width: 950px) {
    .text {
      font-size: 14px;
      letter-spacing: -0.4px; } }

h2 {
  margin: 0;
  line-height: 2;
  font-weight: normal; }
  @media (min-width: 951px) {
    h2 {
      font-size: 24px;
      letter-spacing: -0.8px; } }
  @media (max-width: 950px) {
    h2 {
      font-size: 18px;
      letter-spacing: -0.6px; } }

.small {
  font-weight: 500; }
  @media (min-width: 951px) {
    .small {
      font-size: 14px; } }
  @media (max-width: 950px) {
    .small {
      font-size: 12px; } }

.column {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column; }

a {
  text-decoration: none;
  color: #49c3cf; }
  a:hover, a:active {
    text-decoration: underline; }



